@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap);
.banner {
  position: sticky;
  top: 0;
  padding: 1rem 0;
  background-color: hsl(27, 100%, 50%);
  color: white;
}

.banner h1 {
  font-size: 1.8rem;
}

.link {
  display: inline-block;
  padding-top: 0.5rem;
  padding-right: 1vw;
  font-weight: bold;
}

.activated {
  color: hsl(27, 100%, 30%);
}

@media screen and (min-width: 560px) {
  .banner h1 {
    font-size: 2.5rem;
  }
}

.io-container {
  padding: 2vw 0;
  background-color: hsl(236, 7%, 55%);
  font-size: 0.9rem;
  color: white;
}

.keypad {
  max-width: 45rem;
  border: 0.01rem solid hsl(0, 0%, 73%);
}

.row {
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
}

.key {
  padding: 2vw;
  border: 0.01rem solid hsl(0, 0%, 73%);
  font-size: 0.9rem;
  text-align: center;
  cursor: pointer;
}

.gray-bg {
  background-color: hsl(0, 0%, 88%);
}

.orange-bg {
  background-color: hsl(27, 90%, 60%);
}

@media screen and (min-width: 420px) {
  .key,
  .io-container {
    font-size: 1.5rem;
  }
}

body {
  margin: 0;
  padding: 0;
  color: hsl(0, 5%, 25%);
  font-family: 'Montserrat', sans-serif;
}

h1 {
  margin: 0;
}

